/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu, ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

* {
  box-sizing: border-box;
}

main {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  padding: 2.4rem;
  overflow: auto;
  background: #ecf2f8;
}
main > .card {
  max-width: 37.7rem;
  margin-bottom: 0.7rem;
}
@media (min-width: 49.625rem) {
  main > .card {
    max-width: 73rem;
    margin-bottom: 0;
  }
}

.card {
  display: flex;
  flex-flow: column;
  overflow: hidden;
  font-family: Manrope, sans-serif;
  letter-spacing: 0.01em;
  background: white;
  border-radius: 0.8rem;
  box-shadow: 0 4rem 2.8rem rgba(158, 175, 194, 0.2);
}
@media (min-width: 49.625rem) {
  .card {
    flex-flow: row;
    overflow: visible;
  }
}
.card > header {
  position: relative;
  height: 0;
  padding-bottom: 61.15%;
}
@media (min-width: 49.625rem) {
  .card > header {
    flex: 1 0 28.5rem;
    height: auto;
    padding-bottom: 0;
    overflow: hidden;
    border-top-left-radius: 0.8rem;
    border-bottom-left-radius: 0.8rem;
  }
}
.card > header > img {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 0 -16px;
}
@media (min-width: 49.625rem) {
  .card > header > img {
    object-position: left;
  }
}
.card > .card-body {
  position: relative;
  padding: 3.6rem 3.2rem 2rem;
}
@media (min-width: 49.625rem) {
  .card > .card-body {
    padding: 3.2rem 4rem;
  }
}
.card > .card-body > h2 {
  color: #48556a;
  font-weight: 700;
  font-size: 1.6rem;
  line-height: 1.5;
}
@media (min-width: 49.625rem) {
  .card > .card-body > h2 {
    font-size: 2rem;
    line-height: 1.4;
    letter-spacing: 0.012em;
  }
}
.card > .card-body > p {
  margin-top: 1.2rem;
  color: #6d7f97;
  font-weight: 500;
  font-size: 1.3rem;
  line-height: 1.6;
}
@media (min-width: 49.625rem) {
  .card > .card-body > p {
    letter-spacing: 0.008em;
  }
}
.card > .card-body > .meta {
  margin-top: 3.2rem;
}
@media (min-width: 49.625rem) {
  .card > .card-body > .meta {
    margin-top: 2rem;
  }
}

.meta {
  display: grid;
  grid-gap: 0.5rem 1.6rem;
  grid-template: "avatar name share" auto "avatar date share" auto/auto 1fr auto;
  align-items: center;
  font-size: 1.3rem;
  line-height: 1.2;
  letter-spacing: 0.01em;
}
.meta > img {
  grid-area: avatar;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
}
.meta > p {
  grid-area: name;
  align-self: end;
  color: #48556a;
  font-weight: 700;
}
.meta > time {
  grid-area: date;
  align-self: start;
  color: #9eafc2;
  font-weight: 500;
}
.meta > button {
  z-index: 10;
  grid-area: share;
  background: #ecf2f8;
  border-radius: 50%;
}
.meta > button.inverse {
  background: #6d7f97;
}
.meta > button.inverse > svg > path {
  fill: white;
}
.meta > button.inverse:hover {
  background: #ecf2f8;
}
.meta > button.inverse:hover > svg > path {
  fill: #6d7f97;
}

.iconBtn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 3.2rem;
  height: 3.2rem;
  padding: 0.4rem;
  background: transparent;
  border: none;
  cursor: pointer;
  transition: transform 0.2s, background-color 0.2s;
}
.iconBtn > svg > path {
  transition: fill 0.2s;
}
.iconBtn:hover {
  background: #6d7f97;
  outline: none;
}
.iconBtn:hover > svg > path {
  fill: white;
}
.iconBtn:active {
  transform: translateY(0.2rem);
}
.iconBtn:focus {
  outline: 0.1rem solid #9eafc2;
}

.share {
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  width: 100%;
  padding: 2.4rem 3.2rem;
  background: #48556a;
  transition: visibility 0s, bottom 0.3s ease-out, opacity 0.3s ease-out;
}
@media (min-width: 49.625rem) {
  .share {
    right: 5.6rem;
    bottom: 9.6rem;
    left: auto;
    width: auto;
    padding: 1.2rem 3.1rem 1.1rem 3.7rem;
    border-radius: 0.8rem;
    transform: translateX(50%);
  }
  .share::after {
    position: absolute;
    bottom: -1.2rem;
    left: 50%;
    border-top: 1.2rem solid #48556a;
    border-right: 1.2rem solid transparent;
    border-left: 1.2rem solid transparent;
    transform: translateX(-50%);
    content: "";
  }
}
.share.hidden {
  bottom: -8rem;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 0.3s, bottom 0.3s ease-out, opacity 0.3s ease-out;
}
@media (min-width: 49.625rem) {
  .share.hidden {
    bottom: 8rem;
  }
}
.share > p {
  color: #9eafc2;
  font: 500 1.2rem Manrope, sans-serif;
  letter-spacing: 0.48em;
  text-transform: uppercase;
}
.share > ul {
  display: flex;
  margin: 0;
  margin-left: 1rem;
  padding: 0;
  list-style: none;
}
.share > ul > li {
  display: block;
}
.share > ul > li:not(:first-child) {
  margin-left: 0.4rem;
}
.share > ul > li > button {
  border-radius: 0.6rem;
}